import React, {Component} from 'react'
import Header from './Header'
import Todo from './Todo'

export default class TodoList extends Component {

    constructor(props) {
        super(props)
        this.state = {
            todos: [],
            todoTitle: '',
            status: 'all'
        }

        this.addTodo = this.addTodo.bind(this)
        this.removeTodo = this.removeTodo.bind(this)
        this.editTodo = this.editTodo.bind(this)
        this.todoTitleHandler = this.todoTitleHandler.bind(this)
        this.statusHandler = this.statusHandler.bind(this)

    }

    addTodo(event) {
        event.preventDefault()
        const {todos, todoTitle} = this.state
        if (todoTitle.length > 0) {
            const newTodo = {id: todos.length, title: todoTitle, status: false}
            this.setState((prevState) => {
                return {todos: [...prevState.todos, newTodo], todoTitle: ''}
            })
        }

    }

    removeTodo(idTodo) {
        const {todos} = this.state
        const newTodo = todos.filter(todo => todo.id !== idTodo)
        this.setState({todos: newTodo})

    }

    editTodo(idTodo) {
        const todos = this.state.todos
        const indexTodo = todos.findIndex(todo => todo.id === idTodo)
        todos[indexTodo].status = !todos[indexTodo].status
        this.setState({
            todos: todos
        })
    }

    todoTitleHandler(event) {
        this.setState({todoTitle: event.target.value})
    }

    statusHandler(event) {
        this.setState({status: event.target.value})
    }

    render() {
        return (
            <>
                <Header/>
                <form onClick={this.addTodo}>
                    <input type="text" className="todo-input" maxLength="40" value={this.state.todoTitle}
                           onChange={this.todoTitleHandler}/>
                    <button className="todo-button" type="submit">
                        <i className="fas fa-plus-square"></i>
                    </button>
                    <div className="select">
                        <select name="todos" className="filter-todo" onChange={this.statusHandler}>
                            <option value="all">All</option>
                            <option value="completed">Completed</option>
                            <option value="uncompleted">Uncompleted</option>
                        </select>
                    </div>
                </form>

                <div className="todo-container">
                    <ul className="todo-list">
                        {this.state.status==="all" && this.state.todos.length > 0 && this.state.todos.map(todo => <Todo key={todo.id} {...todo}
                                                                                           onEdite={this.editTodo}
                                                                                           onRemove={this.removeTodo}/>)}
                        {this.state.status==="completed" && this.state.todos.length > 0 && this.state.todos.filter(todo=>todo.status === true).map(todo => <Todo key={todo.id} {...todo}
                                                                                                                        onEdite={this.editTodo}
                                                                                                                        onRemove={this.removeTodo}/>)}
                        {this.state.status==="uncompleted" && this.state.todos.length > 0 && this.state.todos.filter(todo=>todo.status === false).map(todo => <Todo key={todo.id} {...todo}
                                                                                                                                                                 onEdite={this.editTodo}
                                                                                                                                                                 onRemove={this.removeTodo}/>)}
                    </ul>
                </div>
            </>
        )
    }
}
