import React, { Component } from 'react'

export default class Todo extends Component {
    render() {
        const {onEdite,onRemove, id , title,status}=this.props

        return (
            // 'completed' class for completed todos
            <div className={status?'todo completed':'todo'} style={{ display: 'flex' }}>
                <li className="todo-item">{title}</li>

                <button className="check-btn" onClick={()=>onEdite(id)}>
                    <i className="fas fa-check" aria-hidden="true" ></i>
                </button>

                <button className="trash-btn" onClick={()=>onRemove(id)}>
                    <i className="fas fa-trash" aria-hidden="true" ></i>
                </button>
            </div>
        )
    }
}